import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue"

export default {
	components: {
		uniLoadMore
	},
	data() {
		return {
			num: 5,
			more: 'more'
		}
	},
	onLoad() {
	},
	onPullDownRefresh() {
		uni.stopPullDownRefresh();
	},
	onReachBottom() {
		if (this.num > 40) {
			this.more = 'noMore';
		} else {
			this.more = 'loading';
			setTimeout(() => {
				this.more = 'more';
				this.num += 5;
			}, 300);
		}
	},
	methods: {}
}
